import { Injectable } from '@angular/core'
import { LiveUpdate } from '@capawesome/capacitor-live-update'
import { Observable, Subject } from 'rxjs'
import { environment } from 'src/environments/environment'

export type LiveUpdateEvent =
  | 'check_started'
  | 'bundle_found'
  | 'bundle_not_found'
  | 'download_started'
  | 'download_completed'
  | 'reload_started'
  | 'error'

@Injectable({
  providedIn: 'root',
})
export class LiveUpdateService {
  constructor() {}

  // Sujet privé pour émettre les événements
  private updateSubject = new Subject<LiveUpdateEvent>()

  // Observable public pour que les composants puissent s'abonner
  public updates$: Observable<LiveUpdateEvent> =
    this.updateSubject.asObservable()

  // Méthode principale pour vérifier et appliquer les mises à jour
  async checkForUpdate() {
    try {
      this.updateSubject.next('check_started')

      // Recherche du dernier bundle disponible
      const bundle = await LiveUpdate.fetchLatestBundle()
      if (
        bundle &&
        bundle.bundleId &&
        bundle.customProperties?.['timestamp'] &&
        environment.timestamp < bundle.customProperties?.['timestamp']
      ) {
        const currentBundles = await LiveUpdate.getBundles()
        const currentBundle = await LiveUpdate.getCurrentBundle()

        if (
          currentBundles.bundleIds.includes(bundle.bundleId) &&
          currentBundle.bundleId === bundle.bundleId
        ) {
          this.updateSubject.next('bundle_not_found')
          return
        } else {
          this.updateSubject.next('bundle_found')
        }

        // Démarrage du téléchargement du bundle
        this.updateSubject.next('download_started')

        const syncResult = await LiveUpdate.sync()
        if (syncResult.nextBundleId) {
          this.updateSubject.next('download_completed')
          // Pause de 500ms avant le redémarrage pour laisser le temps à l'interface utilisateur de réagir
          // Et que ca ne fasse pas un clignotement non plus
          await new Promise((resolve) => setTimeout(resolve, 500))
          this.updateSubject.next('reload_started')
          await LiveUpdate.reload()
        } else {
          this.updateSubject.next('bundle_not_found')
        }
      } else {
        this.updateSubject.next('bundle_not_found')
      }
    } catch (error) {
      console.error('Erreur lors de la mise à jour LiveUpdate :', error)
      this.updateSubject.next('error')
    }
  }

  // Méthode pour indiquer que l'application s'est chargée
  async ready() {
    await LiveUpdate.ready()
  }
}
