import { Component, effect, OnInit, OnDestroy } from '@angular/core'
import { CommonModule } from '@angular/common'
import {
  IonContent,
  IonImg,
  IonRow,
  IonProgressBar,
  IonCol,
} from '@ionic/angular/standalone'
import { environment } from 'src/environments/environment'
import { StorageService } from '../../services/utils/storage.service'
import { Router, ActivatedRoute } from '@angular/router'
import { AuthService, AuthState } from 'src/app/services/external/auth.service'
import { TranslateModule } from '@ngx-translate/core'
import {
  LiveUpdateEvent,
  LiveUpdateService,
} from 'src/app/services/liveupdate.service'
import {
  AccountService,
  cyclistState,
} from 'src/app/services/api-wrappers/account.service'
import {
  AppState,
  AppstateService,
} from 'src/app/services/internal/appstate.service'
import { Capacitor } from '@capacitor/core'
import { LaunchService } from 'src/app/services/internal/launch.service'
import { KYCWrapperService } from 'src/app/services/api-wrappers/kyc.service'
import { AppTrackingTransparency } from 'capacitor-plugin-app-tracking-transparency'
import { addIcons } from 'ionicons'
import { cloudDownloadOutline } from 'ionicons/icons'

@Component({
  selector: 'app-launch',
  templateUrl: './launch.page.html',
  styleUrls: ['./launch.page.scss'],
  providers: [Storage, StorageService],
  imports: [
    IonContent,
    CommonModule,
    IonRow,
    IonImg,
    TranslateModule,
    IonProgressBar,
    IonCol,
  ],
})
export class LaunchPage implements OnInit, OnDestroy {
  public program: any
  private auth: AuthState = { state: 'SignedOut' }
  private cyclist: cyclistState = {
    state: 'unknown',
    blocked_reasons: ['Unknown account state'],
  }
  private app: AppState
  public isFirstVisit: boolean = false
  public loaderStateText = 'launch.update_checking'
  private nextUrl: string | null = null
  public liveUpdateState: string = 'idle'
  private updateSubscription: any

  constructor(
    private storageService: StorageService,
    private accountService: AccountService,
    private router: Router,
    private authService: AuthService,
    private liveUpdateService: LiveUpdateService,
    private appstateService: AppstateService,
    private route: ActivatedRoute,
    private launchService: LaunchService,
    private kycService: KYCWrapperService
  ) {
    this.route.queryParams.subscribe((params) => {
      if (params['next']) {
        this.nextUrl = params['next']
      }
    })
    effect(() => {
      this.auth = this.authService.authState()
    })
    effect(() => {
      this.cyclist = this.accountService.cyclistState()
    })
    addIcons({
      cloudDownloadOutline: cloudDownloadOutline,
    })
    // Subscribe to update events
    this.updateSubscription = this.liveUpdateService.updates$.subscribe(
      (event) => {
        switch (event) {
          case 'download_started':
            this.liveUpdateState = 'download_started'
            break
          case 'download_completed':
            this.liveUpdateState = 'download_completed'
            break
          case 'error':
          case 'bundle_not_found':
            this.liveUpdateState = 'bundle_not_found'
            break
        }
      }
    )
  }

  ngOnDestroy() {
    if (this.updateSubscription) {
      this.updateSubscription.unsubscribe()
    }
  }

  async ngOnInit() {
    this.app = await this.appstateService.getSavedAppState()

    if (Capacitor.getPlatform() === 'ios') {
      const response = await AppTrackingTransparency.getStatus()
      if (response.status === 'notDetermined') {
        const appTrackingResponse =
          await AppTrackingTransparency.requestPermission()
        if (appTrackingResponse.status === 'authorized') {
        } else {
        }
      }
    }

    // Create a promise that resolves after 5 seconds for the rest of the data
    const timeoutPromise = new Promise((resolve) => setTimeout(resolve, 5000))

    // Race between loadData and timeout
    await Promise.race([this.loadData(), timeoutPromise])

    // On indique au plugin liveUpdate qu'on est ready pour eviter le rollback
    // Si on atteinds pas cet emplacement le plugin rollbackera dans la version de bundle précédente
    if (Capacitor.isNativePlatform()) {
      // Vérification du liveUpdate
      await this.checkForLiveUpdate()
      await this.liveUpdateService.ready()
    }

    if (!this.app) {
      this.appstateService.setAppState({ state: 'onboarding' })
      await this.router.navigate(['/landing'], { replaceUrl: true })
    }
    switch (this.auth.state) {
      case 'SignedIn':
        if (
          this.cyclist.state === 'unknown' ||
          this.cyclist.state === 'error'
        ) {
          await this.waitForCyclistState()
        }
        if (this.app.state === 'onboarding') {
          this.redirectOnboarding()
        } else {
          this.redirect()
        }
        break
      case 'SignedOut':
        await this.router.navigate(['/landing'], { replaceUrl: true })
        break
      default:
        await this.router.navigate(['/map'], { replaceUrl: true })
        break
    }
  }

  async loadData() {
    try {
      // Use LaunchService to load all essential data
      await this.launchService.loadEssentialData({
        skipIfDataExists: false, // Always load on launch page
        forceRefreshStations: true,
      })

      // Get the program for the template
      this.program = await this.storageService.get('program')
    } catch (error) {
      console.error('Error in loadData:', error)
    }
  }

  checkForLiveUpdate() {
    return new Promise<void>(async (resolve, reject) => {
      // Abonnement aux événements de mise à jour
      const subscription = this.liveUpdateService.updates$.subscribe(
        async (event: LiveUpdateEvent) => {
          switch (event) {
            case 'check_started':
              this.loaderStateText = 'launch.update_checking'
              break
            case 'bundle_found':
              this.loaderStateText = 'launch.update_available'
              break
            case 'bundle_not_found':
              this.loaderStateText = 'launch.no_update'
              subscription.unsubscribe()
              resolve()
              break
            case 'download_started':
              this.loaderStateText = 'launch.update_downloading'
              break
            case 'download_completed':
              this.loaderStateText = 'launch.update_downloaded'
              break
            case 'reload_started':
              this.loaderStateText = 'launch.update_app_reloading'
              // resolve() au cas ou mais l'application devrait redemarer
              resolve()
              break
            case 'error':
              this.loaderStateText = 'launch.error'
              subscription.unsubscribe()
              reject('liveUpdateError')
              break
          }
        }
      )

      // Déclenchement de la vérification des mises à jour
      this.liveUpdateService.checkForUpdate()
    })
  }

  async redirectOnboarding() {
    if (this.cyclist.state === 'unconfirmed') {
      if (this.accountService.hasMissingPaymentMethodOrCgu()) {
        await this.router.navigate(['/payment-method'], {
          replaceUrl: true,
        })
      } else if (
        this.kycService.kycCompletionState() === 'missing' ||
        this.kycService.kycCompletionState() === 'rejected'
      ) {
        await this.router.navigate(['/kyc'], {
          replaceUrl: true,
          queryParams: {
            onboarding: 'true',
          },
        })
      } else {
        await this.router.navigate(['/map'], {
          replaceUrl: true,
        })
      }
    } else {
      await this.router.navigate(['/map'], {
        replaceUrl: true,
      })
    }
  }

  async redirect() {
    if (
      this.kycService.kycCompletionState() === 'missing' ||
      this.kycService.kycCompletionState() === 'rejected'
    ) {
      await this.router.navigate(['/kyc'], { replaceUrl: true })
    } else if (this.nextUrl && this.nextUrl !== '/') {
      await this.router.navigate([this.nextUrl], { replaceUrl: true })
    } else {
      await this.router.navigate(['/map'], { replaceUrl: true })
    }
  }

  async waitForCyclistState() {
    const maxRetries = 1000
    for (let i = 0; i < maxRetries; i++) {
      if (this.cyclist.state !== 'unknown' && this.cyclist.state !== 'error') {
        return
      }
      await new Promise((resolve) => setTimeout(resolve, 5))
    }
    console.warn('Cyclist state did not change within the allowed time')
  }
}
